.my-orders
{
    margin: 50px 0;
}
.my-orders .container
{
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 30px;
}
.my-orders-order
{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 2fr 1fr;
    align-items: center;
    gap: 30px;
    font-size: 14px;
    padding: 10px 20px;
    color: #454545;
    border: 1px solid tomato;
}
.my-orders-order img
{
    width: 50px;
}
.my-orders-order p span
{
    color: tomato;
}
.my-orders-order p b
{
    font-weight: 700;
    color: #454545;
}
.my-orders-order button
{
    border: none;
    padding: 12px 0px;
    border-radius: 4px;
    cursor: pointer;
    background-color: #ffe1e1;
    color: #454545;
    font-size: 14px;
}
@media (max-width:900px) {
    .my-orders-order{
        grid-template-columns: 1fr 2fr 1fr;
        gap: 5px;
        font-size: 12px;
    }
    .my-orders-order button{
        font-size: 10px;
    }
}