*
{
    margin: 0;
    padding: 0;
}
.logo
{
    height: 35px;
}
.addtocart
{
    width: 60px;
}
.navbar
{
    padding: 20px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.navbar .logo
{
    width: 150px;
}
.navbar-menu
{
    display: flex;
    list-style: none;
    gap: 20px;
    color: darkblue;
    cursor: pointer;
    font-size: 20px;
    margin-left: 50px;
}
.navbar-right
{
    display: flex;
    align-items: center;
    gap: 35px;
    margin-left: 50px;
}
.navbar button
{
    background: transparent;
    font-size: 17px;
    color: #49557e;
    border: 1px solid tomato;
    padding: 8px 20px;
    border-radius: 50px;
    cursor: pointer;
    transition: 0.3s;
}
.navbar button:hover
{
    background-color: #fff4f2;
}
.navbar .active
{
    padding-bottom: 2px;
    border-bottom: 2px solid #49557e;
    cursor: pointer;
}
.navbar-search-icon
{
    position: relative;
}
.navbar-search-icon .dot
{
    position: absolute;
    min-width: 10px;
    min-height: 10px;
    background-color: tomato;
    border-radius: 5px;
    top: -4px;
    right: -8px;
}
.navbar-profile
{
    position: relative;
}
.nav-profile-dropdown
{
    position: absolute;
    display: none;
    right: 0;
    z-index: 1;
}
.navbar-profile:hover .nav-profile-dropdown
{
    display: flex;
    flex-direction: column;
    gap: 10px;
    background-color: #fff2ef;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid tomato;
    outline: 2px solid white;
    list-style: none;
}
.nav-profile-dropdown li
{
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}
.nav-profile-dropdown img
{
    width: 20px;
}
.nav-profile-dropdown li:hover
{
    color: tomato;
}
@media (max-width:1050px) {
    .navbar .logo{
        width: 140px;
    }
    .navbar-menu{
        gap: 20px;
        font-size: 20px;
    }
    .navbar-right{
        gap: 30px;
    }
    .navbar-right img{
        width: 22px;
    }
    .navbar-right button{
        padding: 8px 25px;
    }
}
@media (max-width:900px) {
    .navbar .logo{
        width: 120px;
    }
    .navbar-menu{
        gap: 15px;
        font-size: 16px;
    }
    .navbar-right{
        gap: 25px;
    }
    .navbar-right img{
        width: 20px;
    }
    .navbar-right button{
        padding: 7px 20px;
        font-size: 15px;
    }
}
@media (max-width:750px) {
    .navbar-menu{
        display: none;
    }
}