.cart{
    margin-top: 100px;
}
.cart-items-title
{
    display: grid;
    grid-template-columns: 1fr 1.5fr 1fr 1fr 1fr 0.5fr;
    align-items: center;
    color: grey;
    font-size: max(1vw 12px);
  
}
.cart-items-item
{
    margin: 10px 0px;
    color: black;
}
.cart-items-item img
{
    width: 50px;
}
.cart hr
{
    height: 1px;
    background-color: #e2e2e2;
    border: none;
}
.cart-items-item button
{
    cursor: pointer;
    padding: 4px;
    border-radius: 3px;
    border: 0.5px solid black;
}
.cart-items-item button p
{
    font-size: 15px;
    outline: none;
    border: none;
}
.cart-bottom
{
    margin-top: 80px;
    display: flex;
    justify-content: space-between;
    gap: max(12vw, 20px);
}
.cart-total
{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.cart-total-details
{
    display: flex;
    justify-content: space-between;
    color: #555;
}
.cart-total hr
{
    margin: 10px 0px;
}
.cart-total button
{
    border: none;
    color: white;
    background-color: tomato;
    width: max(15vw, 200px);
    padding: 12px 0px;
    border-radius: 4px;
    cursor: pointer;
}
.cart-promocode
{
    flex: 1;
}
.cart-promocode p
{
    color: #555;

}
.cart-promocode-input
{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #eaeaee;
    border-radius: 4px;
}
.cart-promocode-input input
{
    background-color: transparent;
    border: none;
    outline: none;
    padding-left: 10px;
}
.cart-promocode-input button
{
    width: max(10vw, 150px);
    padding: 12px 5px;
    background-color: black;
    border: none;
    color: white;
    border-radius: 5px;
}