@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Outfit;
  scroll-behavior: smooth;
}
body
{
  min-height: 100vh;
}
a{
  text-transform: none;
  color: inherit;
  text-decoration: none;
}
.app
{
  width: 80%;
  margin: auto;
}
@keyframes fadeIn {
    0%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
}